.offer{
    width:65%;
    height: 90vh;
    display:flex;
    margin:auto;
    margin-top: 80px;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #fde1ff 0%,#e1ffea22 60%)
}

.offer-left{
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offer-left h1{
    color:#171717;
    font-size:80px;
    font-weight: 600;
}

.offer-left p{
    color:#171717;
    font-size:22px;
    font-weight: 600;
}

.offer-left button{
    width:282px;
    height:70px;
    border-radius: 35px;
    background:#ff4141;
    color:white;
    font-size:22px;
    font-weight:500;
    margin-top:30px;
    cursor:pointer;
}

.offer-right{
    flex:1;
    display:flex;
    align-items: center;
    justify-content: center;
    padding-top:50px
}