.hero{
    height:150vh;
    background: linear-gradient(180deg,#fde1ff,#e1ffea22 60%);
    display:flex;

}
.hero-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; /* Add this line */
    gap: 20px;
   padding-left:80px;
    line-height: 1.1;
  }
  
.hero-left h2{
    color:#090909;
    font-size: 26px;
    font-weight:600 ;
    margin-left: -55%;
}
.hero-left p{
color: #171717;
font-size: 100px;
font-weight:500

}
.hero-hand-icon{
    display:flex;
    align-items: center;
    gap: 20px;
}
.hero-hand-icon img{
    width: 105px;
}
.hero-latest-btn{
    display:flex;
    justify-content: center;
    align-items: center;
    gap:15px;
    width: 310px;
    height:60px;
    border-radius: 75px;
    margin-top:30px;
    background: #ff4141;
    color:white;
    font-size:22px;
    font-weight:500;
}
.hero-right{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
}   
